
@import url("https://fonts.googleapis.com/css?family=Archivo+Black|Quicksand");
body {
  margin: 0;
  padding: 0;
  letter-spacing: 1px;
  font-family: "Quicksand", sans-serif;
}
body * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
body *, body *:before, body *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body .website {
    /*padding: 15px;*/
    width: 100%;
    height: 100vh;
    overflow: auto;
    position: relative;
    border: 15px solid #73e47a;
    /*padding: 75px 15%;*/
}
body .website #top-canvas-line {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      z-index: 11;
      width: 100%;
      position: fixed;
      pointer-events: none;
      background: transparent;
      -webkit-transition: opacity 300ms ease-in-out;
      transition: opacity 300ms ease-in-out;
}
body .website #top-canvas-line.active {
        opacity: 1;
}
body .container {
    display: block;
    margin: 0 auto;
    max-width: 1200px;
    padding: 0 20px;
}
body .global_title {
    position: relative;
    margin: 8rem 0 8rem;
}
body .global_title p {
      margin: 0;
      font-size: 3rem;
      display: inline-block;
}
body .global_title p span {
        z-index: 4;
        background: #fff;
        padding: 0 20px 0 0;
        position: relative;
        display: inline-block;
        color: #73e47a;
}
body .global_title p:before {
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        content: '';
        z-index: 1;
        position: absolute;
        background: #73e47a;
}
body .global_title p:after {
        top: 75%;
        bottom: 20%;
        left: 0;
        width: 10%;
        content: '';
        z-index: 2;
        position: absolute;
        background: #FFF;
        -webkit-animation: title-animation 2s infinite;
                animation: title-animation 2s infinite;
}
@-webkit-keyframes title-animation {
0% {
    left: 0;
}
100% {
    left: 100%;
}
}
@keyframes title-animation {
0% {
    left: 0;
}
100% {
    left: 100%;
}
}
.loading .intro {
  opacity: 0;
  -webkit-transition: opacity 400ms ease-out;
  transition: opacity 400ms ease-out;
}
.loading .intro.active {
    opacity: 1;
}
.holder {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #FFF;
}
.circle-stripes-preloader {
  top: 50%;
  left: 50%;
  position: absolute;
  width: 150px;
  height: 150px;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  -webkit-animation: rotatePreloader 10s infinite linear;
          animation: rotatePreloader 10s infinite linear;
}
@-webkit-keyframes rotatePreloader {
0% {
    -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
            transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
}
100% {
    -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
            transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
}
}
@keyframes rotatePreloader {
0% {
    -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
            transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
}
100% {
    -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
            transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
}
}
.circle-stripes-preloader div {
  top: 50%;
  left: 50%;
  width: 50%;
  position: absolute;
  height: 10%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  -webkit-perspective: 150px;
          perspective: 150px;
}
.circle-stripes-preloader div:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #73e47a;
  -webkit-transform: rotateY(0deg) translateZ(75px);
          transform: rotateY(0deg) translateZ(75px);
  -webkit-transition: -webkit-transform 2s;
  transition: -webkit-transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
  -webkit-animation-name: stripeRotate;
          animation-name: stripeRotate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
@-webkit-keyframes stripeRotate {
0% {
    -webkit-transform: rotateY(0deg) translateZ(75px);
            transform: rotateY(0deg) translateZ(75px);
}
100% {
    -webkit-transform: rotateY(360deg) translateZ(75px);
            transform: rotateY(360deg) translateZ(75px);
}
}
@keyframes stripeRotate {
0% {
    -webkit-transform: rotateY(0deg) translateZ(75px);
            transform: rotateY(0deg) translateZ(75px);
}
100% {
    -webkit-transform: rotateY(360deg) translateZ(75px);
            transform: rotateY(360deg) translateZ(75px);
}
}
.circle-stripes-preloader div:nth-child(1) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(36deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(36deg);
  z-index: 9;
}
.circle-stripes-preloader div:nth-child(1):before {
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(2) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(72deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(72deg);
  z-index: 8;
}
.circle-stripes-preloader div:nth-child(2):before {
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(3) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(108deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(108deg);
  z-index: 7;
}
.circle-stripes-preloader div:nth-child(3):before {
  -webkit-animation-duration: 4.5s;
          animation-duration: 4.5s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(4) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(144deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(144deg);
  z-index: 6;
}
.circle-stripes-preloader div:nth-child(4):before {
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(5) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(180deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(180deg);
  z-index: 5;
}
.circle-stripes-preloader div:nth-child(5):before {
  -webkit-animation-duration: 7.5s;
          animation-duration: 7.5s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(6) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(216deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(216deg);
  z-index: 4;
}
.circle-stripes-preloader div:nth-child(6):before {
  -webkit-animation-duration: 9s;
          animation-duration: 9s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(7) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(252deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(252deg);
  z-index: 3;
}
.circle-stripes-preloader div:nth-child(7):before {
  -webkit-animation-duration: 10.5s;
          animation-duration: 10.5s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(8) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(288deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(288deg);
  z-index: 2;
}
.circle-stripes-preloader div:nth-child(8):before {
  -webkit-animation-duration: 12s;
          animation-duration: 12s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(9) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(324deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(324deg);
  z-index: 1;
}
.circle-stripes-preloader div:nth-child(9):before {
  -webkit-animation-duration: 13.5s;
          animation-duration: 13.5s;
  background-color: #73e47a;
}
.circle-stripes-preloader div:nth-child(10) {
  -webkit-transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
          transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
  z-index: 0;
}
.circle-stripes-preloader div:nth-child(10):before {
  -webkit-animation-duration: 15s;
          animation-duration: 15s;
  background-color: #73e47a;
}
@media (max-width: 768px) {
body .global_title {
    margin: 4rem 0 4rem;
}
body .global_title p {
      font-size: 2rem;
}
.hero .hero__name {
    width: 90%;
}
.hero .hero__name h2 {
      font-size: 2rem !important;
}
.hero .hero__name h2 span {
        padding: 10px 5px !important;
}
}
@media (max-width: 480px) {
body .global_title {
    margin: 2rem 0 2rem;
}
body .global_title p {
      font-size: 1.7rem;
}
}



