
.description {
  border-bottom: 1px solid #dadada;
}
.description .description__wrapper {
    display: block;
    margin: 0 auto;
    width: 100%;
    z-index: 4;
    max-width: 600px;
    padding: 4rem 0 12rem;
    position: relative;
}
.description .description__wrapper__white {
      opacity: 1;
      padding: 20px 40px;
      background: #FFF;
}
.description .description__wrapper__white p {
        color: #8d8d8d;
        font-size: 1.1rem;
        line-height: 180%;
        margin: 0 0 20px 0;
}
.description .description__wrapper__white p span {
          opacity: 0;
          /*min-width: 6px;*/
          margin-right: 6px;
          display: inline-block;
          -webkit-transform: translateY(30px);
                  transform: translateY(30px);
          -webkit-transition: all 600ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
          transition: all 600ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
}
.description .description__wrapper__white p strong {
          color: #000;
}
.description .description__wrapper__white p:last-of-type {
          margin-bottom: 0;
}
.description .description__wrapper__white:before {
        content: '';
        width: 80px;
        height: 2px;
        position: absolute;
        left: 50%;
        bottom: 10rem;
        background: #8d8d8d;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
}
.description__wrapper__white.active {
  /*opacity: 1;*/
}
.description__wrapper__white.active p span {
    opacity: 1;
    /*min-width: 6px;*/
    -webkit-transform: translateY(0);
            transform: translateY(0);
}



