
.hobbies {
  padding: 0 10rem 9rem 10rem;
  /*background: $primary-color;*/
}
.hobbies__title {
    font-size: 2.2rem;
}
.hobbies__squear {
    font-size: 0;
    width: 100%;
    position: relative;
}
.hobbies__squear__wrapper {
      width: 50%;
      top: 0;
      left: 0;
      bottom: 0;
      position: absolute;
      display: inline-block;
      background: #73e47a;
}
.hobbies__squear__wrapper__title {
        bottom: 3rem;
        left: 2rem;
        right: 2rem;
        z-index: 2;
        margin: 0;
        color: #FFF;
        font-size: 2rem;
        font-weight: bold;
        position: absolute;
        font-family: "Quicksand", sans-serif;
        text-align: left;
}
.hobbies__squear__image {
      z-index: 2;
      width: 50%;
      position: relative;
      display: inline-block;
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
}
.hobbies__squear.squear-1 .hobbies__squear__wrapper {
  left: 50%;
}
.hobbies__squear.squear-2 {
  text-align: right;
}
.hobbies__squear.squear-2 .hobbies__squear__wrapper {
    background: #FFF;
}
.hobbies__squear.squear-2 .hobbies__squear__wrapper .hobbies__squear__wrapper__title {
      color: #73e47a;
}
.global_title.small {
  margin-bottom: 2.5rem;
}
@media (max-width: 1200px) {
.hobbies {
    padding: 0 4rem 5rem 4rem;
}
}
@media (max-width: 1024px) {
.hobbies {
    padding: 0 20px 4rem 20px;
}
.hobbies__squear__wrapper__title {
    font-size: 1.5rem;
}
}
@media (max-width: 1024px) {
.hobbies__squear__wrapper__title {
    font-size: 1.2rem;
}
}
@media (max-width: 768px) {
.hobbies__squear__wrapper__title {
    font-size: 1.2rem;
}
.hobbies__squear__wrapper, .hobbies__squear__image {
    width: 100%;
    display: block;
    position: static;
}
.hobbies__squear__image {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.hobbies__squear__wrapper {
    margin-bottom: 20px;
}
.hobbies__squear__wrapper__title {
    position: static;
    padding: 20px 40px;
}
}



