
.knowledge {
  font-size: 0;
}
.knowledge .knowledge__group {
    display: inline-block;
    vertical-align: top;
    width: 33.33%;
}
.knowledge .knowledge__group__title {
      font-size: 2rem;
}
.knowledge .knowledge__group__elements {
      margin-top: 1rem;
}
.knowledge .knowledge__group__elements ul {
        list-style: none;
        margin: 0;
        padding: 0;
}
.knowledge .knowledge__group__elements ul li {
          padding: 10px 0;
          font-size: 1rem;
}
@media (max-width: 768px) {
.knowledge .knowledge__group {
    width: 100%;
}
.knowledge .knowledge__group .knowledge__group__elements ul {
      overflow: hidden;
      margin-bottom: 40px;
}
.knowledge .knowledge__group .knowledge__group__elements ul li {
        width: 50%;
        float: left;
}
}
@media (max-width: 480px) {
.knowledge .knowledge__group .knowledge__group__elements ul li {
    width: 100%;
}
.knowledge .knowledge__group:last-of-type .knowledge__group__elements ul {
    margin-bottom: 0;
}
}



